.nav {
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}
.nav-items {
    padding: 20px;
}
.nav-img {
    width: 100px;
}
.nav-item {
    display: flex;
}

.nav-button {
    padding: 6px;
    list-style: none;
    font-size: large;
}

.restraunt-main {
    width: 100%;
    margin-top: 20px;
    
}


.restraunt-cards {
    display: flex;
    flex-wrap: wrap;
    
}

.restraunt-card {
    padding: 20px ;
    margin: 10px;
}
.restraunt-card :hover{
    border: 1px solid black;
    cursor: pointer;
}
.res-img {
    width: 180px;
    aspect-ratio: 3/2;
}
.restraunt-card h1{
    width: 100px;
    font-size: small;
}

.search {
    margin-bottom   : 20px;
}